        title: "Revision Tips for JEE Main and Advanced",
        image: image3,
        author: 'AKASH JAISWAL, IIT-Dhanbad',
        time: 'OCT 9, 2019',
        content: <div>
            <p><strong>1. Make Use of the Revision Notes and Flashcards Regularly</strong></p>
            <p>While revising any unit go through your previously prepared notes. This process will help you to strengthen concepts in your memory and hence, applying them becomes much easier. If you haven&rsquo;t prepared flashcards in past then don&rsquo;t waste time in preparing them now. Although flashcards would&rsquo;ve made it easier for recollection, your notes will also suffice.</p>
            <p><strong>2. Prepare a Proper Study Time-table and Stick To It</strong></p>
            <p>Understand each and every topic included in the&nbsp;<a href="https://byjus.com/jee/jee-syllabus/">JEE Syllabus</a>&nbsp;and accordingly formulate a proper study plan. Schedule all your breaks, sleep cycle and study time in this timetable and make sure you stick to it. Set goals and include it as a part of your schedule. The goals you set could be on daily or weekly basis depending on your comfort. Set appropriate time limits to practice questions and ensure that it helps in managing time during the actual JEE Exam.</p>
            <p><strong>3. At the time of Revision, Don&rsquo;t Focus on Learning Anything New</strong></p>
            <p>As mentioned above, your revision time should only be used to revise units that you have already learned. Don&rsquo;t focus on learning any new concept at this time because learning anything new may also weaken your confidence at this stage.</p>
            <p><strong>4. Practice ample of Past year&rsquo;s Question papers and Mock Tests:</strong></p>
            <p>After studying certain concepts, it is better to practice questions from the past years&rsquo; question papers. It will help you to test or analyze your preparation level for JEE exams. Solving previous years&rsquo; question papers will help you gain higher confidence and it also enhances your exam temperament. Don&rsquo;t forget to attempt sample mock tests. These tests should be attempted to know how to manage time and pressure of the actual examination. Therefore, you should set conditions as an ideal to the actual situation with no distractions. Practice several numbers of application-based questions from previous papers.</p>
            <p><strong>5. Take Breaks and Ensure a Healthy Sleep Cycle:</strong></p>
            <p>Make sure to take enough scheduled breaks and maintain a regular sleep cycle. It is obviously not possible to study for the whole day or night. Since most of the hard work is done by your brain, therefore, you should give it sufficient time to recuperate. Sometimes you will notice that revisiting difficult questions after a break becomes less strenuous. Sufficient rest also keeps you motivated and will make your stay positive during your preparations.7</p>
            <p>You can follow these revision tips the next time you sit down to study. These will help you prepare well and be ready for the exams.</p>
            <p>Stay tuned with BYJU&rsquo;S for more information on JEE syllabus and best books for JEE Mains and Advanced.</p>
        </div>
